.user-sorting-list {
  padding: 0;
  list-style: none;
  margin-top: 44px;
}

.user-sorting-list__label {
  position: relative;
  font-size: 18px;
  line-height: 30px;
  font-weight: 400;
  color: #283645;
  padding-left: 70px;
}

.user-sorting-list__label::before {
  content: "";
  width: 39px;
  height: 39px;
  left: 0;
  top: -7px;
  box-sizing: border-box;
  position: absolute;
  border: 2px solid #e5e5e5;
  background-color: #ffffff;
  cursor: pointer;
}

.user-sorting-list__label:hover::before {
  border-color: #283645;
}

.user-sorting-list__label:active::before {
  border-color: #d22856;
}

.user-sorting-list__item {
  padding-bottom: 52px;
}

.user-sorting-list__item:nth-child(6) {
  padding-bottom: 0;
}

.user-sorting-list__checkbox:checked + .user-sorting-list__label::before {
  content: "";
  background-image: url("../img/icon-yes-2.svg");
  background-position: 50% 50%;
  background-repeat: no-repeat;
}

@media (min-width: @tablet) {
  .user-sorting-list__item {
    padding-left: 56px;
  }

  .user-sorting-list {
    margin-top: 48px;
  }

  .user-sorting-list__label {
    padding-left: 60px;
  }
}

@media (min-width: @desktop) {
  .user-sorting-list {
    width: 870px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-bottom: 5px;
  }

  .user-sorting-list__item {
    padding-left: 0;
    width: 260px;
  }

  .user-sorting-list__label {
    display: block;
    padding-left: 60px;
  }

  .user-sorting-list__item--order-1 {
    padding-right: 20px;
    order: 1;
  }

  .user-sorting-list__item--order-2 {
    padding-right: 20px;
    order: 4;
  }

  .user-sorting-list__item--order-3 {
    order: 2;
  }

  .user-sorting-list__item--order-4 {
    order: 5;
  }

  .user-sorting-list__item--order-5 {
    order: 3;
  }

  .user-sorting-list__item--order-6 {
    order: 6;
  }
}
