.user-contact {
  margin-top: 32px;
  margin-bottom: 37px;
}

.user-contact__label {
  display: block;
  margin-bottom: 6px;
}

.user-contact__item {
  display: flex;
  flex-direction: column;
}

@media (min-width: @tablet) {
  .user-contact__input {
    position: relative;
    order: 1;
  }

  .user-contact__title {
    width: 230px;
  }

  .user-contact__conteiner {
    width: 496px;
    padding: 0;
    margin: 0;
  }

  .user-contact__item {
    position: relative;
    padding-left: 58px;
    margin: 0;
  }

  .user-contact__item-tel::before {
    content: url("../img/icon-phone.svg");
    position: absolute;
    display: block;
    top: 0;
    left: 496px;
    width: 62px;
    height: 62px;
    padding-top: 19px;
    padding-left: 22px;
    box-sizing: border-box;
    background-color: #e5e5e5;
    z-index: 40;
  }

  .user-contact__item-email::before {
    content: url("../img/icon-mail.svg");
    position: absolute;
    display: block;
    top: 0;
    left: 496px;
    width: 62px;
    height: 62px;
    padding-top: 19px;
    padding-left: 22px;
    box-sizing: border-box;
    background-color: #e5e5e5;
    z-index: 40;
  }

  .user-contact__label {
    margin: 0;
    padding: 0;
    margin-top: 7px;
    margin-left: 18px;
    order: 2;
  }

  .user-contact__item-tel {
    margin-bottom: 30px;
  }

  .user-contact {
    margin-bottom: 25px;
  }
}

@media (min-width: @desktop) {
  .user-contact {
    width: 940px;
    margin-top: 30px;
    margin-bottom: 0;
  }

  .user-contact__conteiner {
    display: flex;
    padding: 0;
    margin: 0;
  }

  .user-contact__title {
    width: 260px;
    margin-left: 336px;
  }

  .user-contact__item {
    padding-left: 70px;
    margin-bottom: 14px;
  }

  .user-contact__item-tel {
    padding-left: 58px;
  }

  .user-contact__label {
    margin: 0;
    padding: 0;
    margin-top: 6px;
    margin-left: 19px;
  }

  .user-contact__item-tel::before {
    content: url("../img/icon-phone.svg");
    position: absolute;
    display: block;
    top: 2px;
    left: 370px;
    width: 60px;
    height: 58px;
    padding-top: 19px;
    padding-left: 22px;
    box-sizing: border-box;
    background-color: #e5e5e5;
    z-index: 40;
  }

  .user-contact__item-email::before {
    content: url("../img/icon-mail.svg");
    position: absolute;
    display: block;
    top: 2px;
    left: 382px;
    width: 60px;
    height: 58px;
    padding-top: 19px;
    padding-left: 22px;
    box-sizing: border-box;
    background-color: #e5e5e5;
    z-index: 40;
  }
}
